.card {
    padding: 20px;
    border-radius: 8px;
    max-width: 95%;
}

.heading {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
}

.info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}

.weather__temperature {
    font-size: 48px;
    font-weight: bold;
}

.weather__temp-unit {
    font-size: 24px;
}

.weather__condition {
    display: flex;
    align-items: center;
}

.weather__condition-text {
    margin-right: 10px;
}

.weather__condition-icon {
    width: 40px;
    height: 40px;
}

.details {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
}

.weather__detail-item {
    display: flex;
    align-items: center;
}

.weather__detail-label {
    font-weight: bold;
    margin-right: 5px;
}

.weather__detail-value {
    font-weight: normal;
}